






import { Component, Prop, Vue } from 'vue-property-decorator'

interface HelloWorldProps {
    click: () => void
}

@Component
export default class HelloWorld<HelloWorldProps> extends Vue {
    @Prop() drop = false
    style = {
        '--y': '0',
        '--x': '0'
    }
    onClick(e: Event) {
        this.$emit('click', e)
    }
    onMouseMove(e: { offsetX: number; offsetY: number }) {
        this.style = {
            '--y': e.offsetY + 'px',
            '--x': e.offsetX + 'px'
        }
    }
}
